import { createRouter, createWebHistory } from "vue-router";
import api from "@/api/axios";
import LoginPage from "../components/LoginPage.vue";
import NewUser from "../components/NewUser.vue";
import UserDashboard from "../components/UserDashboard.vue";
import UserDetails from "../components/UserDetails.vue";
import UserAddress from "../components/UserAddress.vue";
import UserIncome from "../components/UserIncome.vue";
import UserDocument from "../components/UserDocument.vue";
import VerifyOTP from "../components/VerifyOTP.vue";

const routes = [
  { path: "/login", component: LoginPage },
  { path: "/new-user", component: NewUser },
  { path: "/dashboard", component: UserDashboard },
  { path: "/user-details", component: UserDetails },
  { path: "/user-address", component: UserAddress },
  { path: "/user-income", component: UserIncome },
  { path: "/user-document", component: UserDocument },
  { path: "/verify-otp", component: VerifyOTP },
  { path: "*", redirect: "/login" }, // Redirect unknown routes to login
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

async function checkUserState() {
  try {
    const response = await api.get("/check_user_state");
    return response.data; // Make sure this returns a string that matches expected states
  } catch (error) {
    console.error("Error checking user state:", error);
    return null; // Return null or "login" to handle error cases
  }
}

router.beforeEach(async (to, from, next) => {
  const userState = await checkUserState();

  // Handle infinite redirect scenario
  // if (to.path === "/login" && userState === "login") {
  //   return next(); // Stay on the login page
  // }

  switch (userState) {
    case "user":
      next("/user-address");
      break;
    case "address":
      next("/user-income");
      break;
    case "income":
      next("/user-document");
      break;
    case "documents":
      next("/dashboard");
      break;
    default:
      next("/login"); // Redirect to login for unexpected states
      break;
  }
});

export default router;
