<template>
  <div class="user-dashboard">
    <h1>User Dashboard</h1>
    <div v-if="cardDetails">
      <h2>Your Card Details</h2>
      <p>Card Number: {{ cardDetails.card_number }}</p>
      <p>Expiry Date: {{ cardDetails.expiry_date }}</p>
      <p>CVV: {{ cardDetails.cvv }}</p>
      <p>Spending Limit: ${{ cardDetails.spending_limit }}</p>
    </div>
    <div v-else>
      <p>You don't have a card yet.</p>
      <button @click="createCard" :disabled="isLoading">
        {{ isLoading ? "Creating Card..." : "Create Card" }}
      </button>
    </div>
    <p v-if="error" class="error">{{ error }}</p>
  </div>
</template>

<script>
import api from "@/api/axios";

export default {
  name: "UserDashboard",
  data() {
    return {
      cardDetails: null,
      isLoading: false,
      error: null,
    };
  },
  methods: {
    async createCard() {
      this.isLoading = true;
      this.error = null;
      try {
        const token = localStorage.getItem("access_token");
        const response = await api.post(
          "/user_creditcard",
          {},
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        );
        this.cardDetails = response.data;
      } catch (error) {
        console.error("Error creating card:", error);
        this.error =
          error.response?.data?.detail ||
          "An error occurred while creating the card.";
      } finally {
        this.isLoading = false;
      }
    },
  },
};
</script>

<style scoped>
.user-dashboard {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
}

button {
  background-color: #4caf50;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}

button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

.error {
  color: red;
  margin-top: 10px;
}
</style>
